.AllEmple {
    background-color: #F2F0F8;
    width: 100vw;
}

.ContaiEmpleadores {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 87%;
    min-height: 100vh;
    padding-top: 2rem;
    margin-top: 33rem;
}

.RowMenuBar {
    width: 100%;
}

.RowMenuBar img {
    width: 8.5rem;
}

.divMenuOptions {
    display: flex;
    width: 65.8rem;
    align-items: center;
    justify-content: space-between;
    margin-left: 4rem;
}

.RowBusqueda {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: left;
}

.RowBusqueda h1 {
    color: #7583C1;
    font-size: 3.6rem;
    font-weight: 800;
    width: 50rem;
    margin-left: 1rem;
    margin-top: 1.5rem;
}

.CheckboxGroup {
    margin-left: 1rem;
    color: #86888A;
}

.RowBusqueda .o-search-container {
    width: 100%;
    height: 3rem;
    border-radius: 3rem;    
    margin: 0.5rem 0 1.5rem 0;
    padding: 0 1.3rem;
}

.divBSearch {
    margin-left: 1rem;
}

.RowRankingCertificados {
    width: 100%;
    padding: 0 1rem;
}

.RowRankingCertificados h3 {
    color: #6D929E;
    margin-top: 2rem;
    font-weight: bold;
}

.RowRankingCertificados p {
    color: #414042;
    font-size: 1.2rem;
    margin-top: 0.2rem;
}

.RowRankCards {
    margin-left: 0;
    width: 100%;
    display: flex;
    justify-content: space-around;    
}

.RowRankC {
    width: 100%;
    padding: 0 1rem;
    margin-bottom: 2rem;
}

.RowRankC h3 {
    margin-top: 3rem;
    color: #6D929E;
    font-weight: lighter;
    margin-bottom: 1.3rem;
}

.RowRankC span {
    font-weight: bold;
}