.divNotItem {
    background-color: #E6E7E8;
    border-radius: 0.5rem;
    padding: 0.7rem 1rem;
    display: flex;
    align-items: center;
    margin: 1rem 0;
    width: 70rem;
}

.divIcon {
    background-color: #725FA9;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 2rem;
}

.divIcon i {
    color: white;
    font-size: 2rem;
}

.divNotItem p {
    margin: 0;
    color: #414042;
    font-size: 1.2rem;
    width: 58.3rem;
}

.divPlusIcon {
    font-size: 1.6rem;
    color: #6D929E;
    margin-left: 2rem;
}