.o-card-cursos {
    background-color: #E6E7E8;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    padding: 0.5rem 1.5rem;
    width: 16rem;    
    margin-right: 2rem;
}

.o-box-img-curso {
    border-radius: 1rem;
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1rem;
}

.o-box-img-curso img {
    width: 4rem;
}

.o-card-cursos P {
    font-size: 1.2rem;
    color: #86888A;
    font-weight: 500;
    margin-top: 0.8rem;
}