.o-box-container{
    width: 6em;
    margin: .7em 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.o-box-img{
    height: 6em;
    width: 6em;
    border-radius: .7em;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.o-box-img img{
    width: 70%;
}

.o-box-container p{
    margin: .5em 0;
    font-size: .8em !important;
    color: #86888A !important;
    font-family: Medium;
    text-align: center;
}


/*Estilos del toggle*/
.switch {
    position: relative;
    display: inline-block;
    width: 30%;
    height: 1.1em;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: .8em;
    width: .8em;
    left: 2px;
    bottom: 2.5px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(11px);
    -ms-transform: translateX(11px);
    transform: translateX(11px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

