.ContaiNotFound {
    background-color: #F2F0F8;
    min-height: 100vh;
    display: flex;
    font-family: 'Montserrat', sans-serif;
}

.ColMenu {
    position: absolute;
    padding: 0;
    width: 80vw;
    height: 100vh;
    z-index: 1;
}

.ColInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10rem;
}

.ColInfo h1 {
    font-size: 3.5rem;
    font-weight: 900;
    color: #7583C1;
    margin-bottom: 3rem;
}

.ColInfo h2 {
    color: #6D929E;
    font-size: 2.3rem;
}