.o-menu-panel{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.o-menu-content{
    margin-top: .8em;
    height: 90%;
    width: 6.5em; /*90%*/
    border-radius: 4em;
    background-color: var(--blue_button);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;    
}

.o-menu-option{
    height: 13%; /*4.5em*/
    width: 80%; /*5.5em*/
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: .7em 0;    
}

.o-menu-option img{
    width: 1.5em;
    margin-bottom: .5em;
}

.o-menu-option a{
    text-decoration: none !important;
}

.o-menu-option:hover {
    transform: scale(1.1);
}

.o-menu-option p{
    margin: 0;
    font-size: .7em;
    color: white;
    font-family: Medium;    
}

.o-exit-button{
    background-color: var(--background_login);
    border:none;
    cursor: pointer;
    border-bottom-right-radius: 10rem;
    border-top-right-radius: 10rem;
    font-size: 1rem;
    color:  #2C464F;
    text-decoration: none;
    font-family: Medium;
    height: 2.5em;
    margin-left: -3em;
    text-align: right;
    padding-right: 3em;
}

.o-exit-button a{
    text-decoration: none;
    color:  #2C464F;
    text-decoration: none;
    font-family: Medium;
    height: 2.5em;
    margin-left: -3em;
    text-align: right;
}

.o-exit-button a:hover{
    color: red;
    border: 2px solid var(--background_login);
    text-decoration: none;
}

.o-exit-button:focus{
    border:none;
    outline: none;
}

.o-exit-button i {
    margin-right: 0.5rem;
}