.ContaiProfile {
    min-height: 100vh;
    display: flex;
    background-color: #F2F0F8;
}

.ColMenuP {
    position: absolute;
    padding: 0;
    width: 80vw;
    height: 100vh;
    /* z-index: 1; */
}

.ColInfoPerfil {
    /* padding-left: 20rem; */
    margin-left: 14rem;
    text-align: left;
    /* width: 90vw; */
}

.ColInfoPerfil .o-tittle-container {
    height: inherit;
}

.o-panel-info {
    height: inherit;
}

.DivEditProfile {
    display: flex;
    margin-top: 0.8rem;
}

.DivEditProfile h3 {
    font-weight: bold;
    font-size: 1.4rem;
    margin-right: 0.7rem;
    color: #414042;
}

.DivEditProfile i {
    font-size: 1.4rem;
    color: #6D929E;
}

.ColProfileDescrip {
    padding: 0;
}

.ColProfileDescrip p {
    font-size: 1.1rem;

}

.RowCertificados {
    display: flex;
    flex-direction: column;
    margin-top: 0.7rem;
}

.RowCertificados h3 {
    font-weight: bold;
    font-size: 1.4rem;
    color: #414042;
}

.RowCardsCer {
    margin-top: 0.8rem;
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
    flex-wrap: wrap;
}

.RowPruebasCurso {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
}

.RowPruebasCurso h3 {
    font-weight: bold;
    font-size: 1.4rem;
    color: #414042;
}

.RowCursos {
    margin-top: 0.8rem;
    padding-left: 1rem;
    display: flex;
    flex-wrap: wrap;
}

.ColInfoPerfil .o-tittle-container {
    margin-top: 3rem;
}

.ColInfoPerfil .o-tittle-container h2 {
    margin-bottom: 2.1rem;
}